<script>
import Header from '@/components/common/Header.vue';

export default {
	name: 'Resume',
	components: {
		Header
	},
	data() {
		return {
			status: true,
			tableData: [],
			 checkList: [''],
			multipleSelection: [],
			options: [
				{
					value: '1',
					label: ''
				},
				{
					value: '2',
					label: ''
				},
				{
					value: '3',
					label: ''
				},
				{
					value: '4',
					label: ''
				},
				{
					value: '5',
					label: ''
				}
			],
			value: '',
			radio: '',
			radio1: '',
			radio2: 3,
			posKey: '',
			minAge:'',
			maxAge:'',
			total: 0,
			dialogTableVisible: false,
			checkindex: '',
			listOne: [],
			educationName:'',
			page:1,
			gridData:[],
		};
	},
	created() {
		console.log('');
		if (this.$cookies.isKey('access_token')) {
			//
			console.log('');
		} else {
			//
			this.$router.push({ name: 'Login', params: { userId: '123' } });
			console.log('');
		}
		this.getEmGroup();
		this.categoryList();
	},
	methods: {
		getEmGroup: function() {
			
			
			var data = {
				accessToken: '1565742674|145B1691263AEC04CC1722BA2EF68A86',
				id_token: this.$cookies.get('access_token'),
				// resumeCateId:parseInt(this.resumeCateId),
			};
		
			var _this = this;
			this.$http
				.getEpResumeListByCate1(data)
				.then(res => {
					
					console.log(res)	
					if (res.errorCode == 0) {
						_this.gridData = res.data.list;
						// _this.emNum=res.data.emNum;
						// _this.reviewNum=res.data.reviewNum;
					}
					
				})
				.catch(err => {
					console.log(err);
				});
		},
		gose(){
			if(this.minAge<1){return}
			if(this.maxAge<1){return}
			
			this.categoryList()
		},
		//
		search(){
			if(this.posKey<1){
				this.$message({
					message: '',
					type: 'warning',
					offset:'100'
				});
				return;
			}
			this.categoryList()
			
		},
		//
		work(){
			console.log(this.radio);
			this.categoryList()
		},
		sexone(){
			this.categoryList()
		},
		xueli(){
			console.log(this.checkList)
			for(var i=0;i<this.checkList.length;i++){
				if(this.checkList[i]==''){
					this.checkList=['']
				}
			}
			
			
		},
		xx(){
			this.checkList=[''];
			this.educationName='';
			this.categoryList()
		},
		xx1(){
			var index = this.checkList.indexOf(''); 
			if (index > -1) { 
			this.checkList.splice(index, 1); 
			}
			var a='';
			for(var i=0;i<this.checkList.length;i++){
				if(i!=this.checkList.length-1){
					var b=this.checkList[i]+',';
					a=a+ b
				}else{
					var b=this.checkList[i];
					a=a+ b
				}
			}
			this.educationName=a;
			this.categoryList()
			
		},
		categoryList() {
			this.$_loading = this.$loading({
				lock: true,
				text: 'Loading',
				spinner: 'el-icon-loading',
				background: 'rgba(0, 0, 0, 0.7)'
			});
			var data = {
				accessToken: '1565742674|145B1691263AEC04CC1722BA2EF68A86',
				id_token: this.$cookies.get('access_token'),
				posKey:this.posKey,
				wxWorkLocation: '',
				workExp: this.radio,
				educationName: this.educationName,
				minAge: this.minAge,
				maxAge: this.maxAge,
				sex: this.radio1,
				pageIndex: parseInt(this.page),
				pageSize: parseInt(10)
			};

			var _this = this;
			this.$http
				.resume(data)
				.then(res => {
					console.log(res);
					_this.$_loading.close();
					if (res.errorCode == 0) {
						var xx = res.data.page;
						for (var i = 0; i < xx.length; i++) {
							if (xx[i].sex == 0) {
								xx[i].sex = '';
							} else {
								xx[i].sex = '';
							}
							// xx[i].iid = i;
						}

						this.tableData = xx;

						this.total = res.data.total;
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		onRowClick: function(row, rowIndex) {
			console.log(row);
			console.log(rowIndex);
		},
		handleCurrentChange(e) {
			console.log(e);
			this.page = e;
			this.categoryList();
		},
		toggleSelection(rows) {
			if (rows) {
				rows.forEach(row => {
					this.$refs.multipleTable.toggleRowSelection(row);
				});
			} else {
				this.$refs.multipleTable.clearSelection();
			}
		},
		goAch: function(e) {
			this.dialogTableVisible = true;
			this.checkindex = e;
			console.log(e);
		},
		handleClick(row) {
			console.log(row);
			this.dialogTableVisible = true;
			this.listOne = row;
		},
		handleSelectionChange(val) {
			console.log(val);
			this.multipleSelection = val;
		},
		one() {
			this.status = true;
		},
		one1() {
			this.status = false;
		},
		//
		check(command){
			
			console.log(command)
			var list=this.gridData[command];
			
			//
			if (this.multipleSelection.length == 0) {
				this.$message({
					message: '',
					type: 'warning',
					offset: '100'
				});
				return;
			}
			//
			
			var num=this.multipleSelection;
			var arr=[];
			for(var i=0;i<num.length;i++){
				var data={
					"idCard":num[i].idCard,
					"phone":num[i].phone,
				}
				
				arr.push(data)
				
			}
			
			
			this.$_loading = this.$loading({
				lock: true,
				text: 'Loading',
				spinner: 'el-icon-loading',
				background: 'rgba(0, 0, 0, 0.7)'
			});
			var data = {
				accessToken: '1565742674|145B1691263AEC04CC1722BA2EF68A86',
				id_token: this.$cookies.get('access_token'),
				resumeCateId: parseInt(list.id),
				resumeJson:JSON.stringify(arr)
			};
			
			var _this = this;
			this.$http
				.downLoadMultiResume(data)
				.then(res => {
					console.log(res);
					console.log(123456);
					_this.$_loading.close();
					_this.getEmGroup();
					if (res.errorCode == 0) {
						setTimeout(function() {
							_this.$message({
								message: '',
								type: 'success',
								offset: '100',
								duration: 3000
							});
						}, 1000);
					}
				})
				.catch(err => {
					console.log(err);
				});
			
		}
	}
};
</script>

